import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-article-case-study.css"

export default function Home() {
  const description = `神奈川のIT相談ならホットソウルにお任せください！当社がデジタル化やIT活用でサポートさせていただいた実績をご紹介します。 | 2025年5月 荻田石油株式会社様 「事業承継とデジタル化等経営相談」`
  const pageUrl = `https://www.hotsoul.jp/case-study/ogita-sekiyu`
  const imageUrl = `https://www.hotsoul.jp/ogp_ogita-sekiyu.jpg`

  return (
    <Layout>
      <Seo title="サポート実績" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">

        <section className="mv_ac-cs">
          <div className="mv_ac-cs-inner">
            <p className="txt1"><span>サポート実績</span></p>
            <p className="txt2">当社がデジタル化や<br className="sp"/>IT活用で<br className="pc"/>サポートさせていただいた<br className="sp"/>実績をご紹介します。
            </p>
          </div>
        </section>

        <section className="ac-cs">
          <h2>事業承継とデジタル化等経営相談</h2>
          <div className="ac-cs-inner">
            <div className="flex">
              <div className="flex-item left">
                <StaticImage
                  src="../../images/main-case-study/ogita-sekiyu/ph1.jpg"
                  alt="荻田石油株式会社 様"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <p className="date">2025.5.23</p>
                <p className="title"><span className="bg-yellow">荻田石油株式会社 様<br/></span></p>
              </div>
            </div>
            <div className="flex-table-box">
              <div className="flex-table first">
                <div className="flex-table-item left">
                  <p>お悩み</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>事業継承にあたり、様々な不安要素について意見を伺いたい</li>
                    <li>今までアナログだった部分を可能な限りデジタル化したい</li>
                    <li>この先の経営について多角的に相談したい</li>
                  </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>サポート</p>
                </div>
                <div className="flex-table-item right">
                 <ul>
                   <li>現状の不安やお悩みについて、じっくりと対面でヒアリング</li>
                   <li>大手石油会社の中期経営計画等から業界全体の展望を読み解き共有</li>
                   <li>活用できそうな補助金の情報を助言</li>
                 </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>結果</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>一対一で向き合って話し合うことで、様々な不安や悩みが解消できた</li>
                    <li>デジタル化を進める業務、アナログで残す業務の整理ができた</li>
                    <li>補助金を活用したデジタル化推進のイメージも明確になった</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">サポートの背景</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">まずは自己紹介と御社のご説明をお願いします。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">出光興産の販売店（ガソリンスタンド）として昭和35年に祖父が創業しました。私で3代目になります。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">どのような事情で外部のサポートが必要と思われましたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">現在、事業継承にあたって、社長である父、主に経理を担当している母から様々な引継ぎを受けている段階です。その過程において、アナログな経理処理および雑務が思ったより多いと感じた点が一点。また、いわゆる斜陽産業であるガソリンスタンドの未来について、この先経営が成り立っていくのか、という漠たる不安を吐露したかったというのがもう一点です。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">弊社にご相談いただいたきっかけは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">前職で3年弱同じ職場にいて、人柄や仕事ぶりはよく分かっていたのと、近年は中小企業診断士としても活動されていることを知っていたので。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ogita-sekiyu/ph2.jpg"
                alt="サポートの背景"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">相談内容とサポート</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">ご相談内容は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">先ほど申し上げた通りですが、主に経理処理においてどの程度ITを活用したらよいのかと、また、今後我々のような中小企業の未来について、マクロ、ミクロ両視点で、中長期どのような成長ビジョンをもったらいいのか、こちらの意見や見識を聞いてもらいつつ、専門家の話を伺いたい、ということでした。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">特にお困りの点は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">経理の知識があまりない状態でしたので、一点目の経理の引継ぎについてでした。どうせだったら最初からデジタル化したほうが二度手間にならないのか等、色々考えがまとまらなく困っていました。また、今後の課題ではありますが、弊社の税理士さんが使用しているソフトへの入力を、手入力ではなくcsvファイルで自動取り込みができるようにと思案中なので、その具体的な実現イメージについて教えて欲しかったです。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ogita-sekiyu/ph3.jpg"
                alt="相談内容とサポート"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">今後の展望</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今回、弊社のサポートを受けてみて良かった点をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">各種事務処理において、デジタル化すべきもの（できるもの）と、このままアナログで良いものとを、切り分ける判断基準が明確にできた点。また、様々な課題や不安要素を一対一で問答できた点が良かったです。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">逆に、改善点や期待することがあれば。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">しいて言うならですが、税務の知識をさらに蓄えていただければその面においても深く相談でき、より多角的なお話ができたかと思いました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">荻田石油様の今後の展望をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">荻田さま：</div>
                    <div className="flex-support-item right">地域のインフラをより長く支え、私を含め従業員もその家族も、全員が精神的、経済的になんの憂いもなく愉快な一生を送れるような会社づくりを目指して頑張ります。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ogita-sekiyu/ph4.jpg"
                alt="今後の展望"
                placeholder="blurred"
              />
			        <div className="flex-share">
                <ul className="sns">
                  <li>
                    <a href="https://twitter.com/share?url=https://www.hotsoul.jp/case-study/ogita-sekiyu" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-tw.png"
                        alt="twitterシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li> 
                  <li>
                    <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/case-study/ogita-sekiyu" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-fb.png"
                        alt="facebookシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                  <li>
                    <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/case-study/ogita-sekiyu" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-line.png"
                        alt="LIMEシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                </ul>
			        </div>
              <br/>                
              <p className="archive"><Link to={`/case-study/articles`}>他の記事も読む</Link></p>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}